import Constants from './constant.js';

var constants = new Constants();

export default class EligibilityRequestService {
  async getInsuranceBilling() {
      const res = await fetch(constants.billing_url, {
      headers: {
        "Authorization": constants.token
      }
    });
    const d = await res.json();
    return d.data;
  }

  async sendEligibilityRequest(id){
      var formData = new FormData();
      formData.append('billing-id',id)
      const res = await fetch(constants.send_eligibility_request_patient_healthcare, {
      method: "POST",
      headers: {
        "Authorization": constants.token,
      },
      body: formData
    }).then(response => response.json()).then(result => result).catch((error)=> error);
    return res;
  }

  async sendEligibilityRequestManual(document_id,location_id,start_date,end_date,purpose){
      console.log(purpose)
      var formData = new FormData();
      formData.append('document_id',document_id);
      formData.append('eligibility_purpose',purpose);
      formData.append('location_id',location_id);
      formData.append('start_date',constants.convertDateFormat(start_date));
      formData.append('end_date',constants.convertDateFormat(end_date));
      const res = await fetch(constants.send_eligibility_request_manual, {
      method: "POST",
      headers: {
        "Authorization": constants.token,
      },
      body: formData
    }).then(response => response.json()).then(result => result).catch((error)=> error);
    return res;
  }

}
